@import "./variables.less";
.App {
	text-align: center;
	overflow-x: hidden;
	background-color: #fbfaf5;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.navbar {
	position: fixed;
	top: 0;
	width: 100%;
	color: #fff;
	// background-color: #fff;
	background: rgba(255, 255, 255, 0);
	// box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
	padding: 20px 0 0;
	height: 56px;
	z-index: 1;
	box-sizing: unset;
	/* Animations */
	transition-timing-function: ease-in;
	transition: all 0.5s ease;
}
// to transparent navbar on scroll
.navbar_black {
	background: rgba(191, 191, 191, 0.6);
	// background-color: #fff;
	display: none;
}

.navbar-container {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	margin: 0 auto;
	padding: 0 50px;
	position: relative;
	top: 50%;
	transform: translateY(-50%);
}

.logo {
	color: #fff;
	font-size: 1.5rem;
	text-decoration: none;
	.logoimg {
		width: 30px;
		border-radius: 50%;
	}
}

.nav-links a {
	color: #fff;
	text-decoration: none;
	margin: 0 20px;
	font-size: 1rem;
}

.nav-links a:hover {
	text-decoration: underline;
}
.HeadingOfAll {
	font: normal normal normal 44px/50px "Poppins";
	text-transform: uppercase;
	text-shadow: 2px 2px 4px #616161;
}

.active {
	text-decoration: underline;
}
/* Example styles for the content components */
.container {
	max-width: 800px;
	margin: 50px auto;
	text-align: center;
	color: #333;
}
.logos {
	width: 32px;
	height: 32px;
	cursor: pointer;
}
.logos:hover {
	transition: all 0.2s ease-in-out;
	scale: 1.2;
}
// .IconLinks {
// 	transition: transform 0.3s ease;
// }
.IconLinks::hover {
	cursor: pointer;
}

@media screen and (max-width: @mobile_width) {
	.navbar {
		background: rgba(255, 255, 255, 0.2);
		padding-top: 10px;
	}
	.navbar-container {
		padding: 0 20px;
		justify-content: space-around;
		.nav-links {
			display: flex;
			flex-direction: row;
			gap: 5%;
			justify-content: center;
			a {
				margin: 0 10px;
			}
		}
	}
}
